import React, { Component } from 'react';
import axios from 'axios';
import { Link } from 'react-router-dom';

class Show extends Component {


    constructor(props) {
        super(props);
        this.state = {
            ingredient: {}
        };
    }

    componentDidMount() {
        var global_url="http://localhost:8080"
        axios.get(global_url + '/ingredient/'+this.props.match.params.id)
            .then(res => {
                this.setState({ ingredient: res.data });
                console.log(this.state.ingredient);
            });
    }

    delete(id){
        console.log(id);
        axios.delete('/ingredient/'+id)
            .then((result) => {
                this.props.history.push("/")
            });
    }

    render() {
        console.log("Here" + this.state.ingredient.name);
        return (
            <div class="container">
                <div class="panel panel-default">
                    <div class="panel-heading">
                        <h3 class="panel-title">
                            Ingredient Details
                        </h3>
                    </div>
                    <div class="panel-body">
                        <h4><Link to="/"><span class="glyphicon glyphicon-th-list" aria-hidden="true"></span> Ingredients List</Link></h4>
                        <dl>
                            <dt>Name:</dt>
                            <dd>{this.state.ingredient.name}</dd>
                            <dt>Interactions:</dt>
                            <dd>{this.state.ingredient.interactions}</dd>
                        </dl>
                        <Link to={`/edit/${this.state.ingredient.id}`} class="btn btn-success">Edit</Link>&nbsp;
                        <button onClick={this.delete.bind(this, this.state.ingredient.id)} class="btn btn-danger">Delete</button>
                    </div>
                </div>
            </div>
        );
    }
}

export default Show;